.button {
  color: #fff;
  cursor: pointer;
  border: 1px solid #5f5c5c;
  border-radius: 10px;
  margin: auto;
  padding: 10px 20px;
  font-size: 30px;
  display: block;
}

.button.stop {
  background-color: #db4d4d;
}

.button.start {
  background-color: #4ddb52;
}

